import React, { Fragment, useState } from 'react';
import './component.css';
//emailjs to send email from the website
import emailjs, { init } from 'emailjs-com';

init("user_rB8qWaGWX9OFw575diXB3");

function Contact(){

    const frmContact = { userEmail:'', nameEmail:'', emailDetails:'' };
    const [contact,setContact] = useState(frmContact);
    const [showMessage, setShowMessage] = useState(false);
    const handleChange = e => { 
            const {name,value} = e.target;
            setContact({...contact,[name]:value}); 
    };
    const handleSubmit = e =>{
            e.preventDefault();
        
            emailjs.send('service_nt2v9sp','template_d9g26wn', contact, 'user_rB8qWaGWX9OFw575diXB3')
            .then((response) => {
                    console.log('SUCCESS!', response.status, response.text);
                    setContact(frmContact);
                    setShowMessage(true);
            }, (err) => {
                    console.log('FAILED...', err);
            });
    }

    return(
        <Fragment>
            <h2 className="display-3" id="contact">Contact</h2>
            
            <div className="bg-contact mb-5 input-group-lg">
                <form className="container" onSubmit={handleSubmit}>
                <label><strong>Name</strong></label>
                    <input className="form-control"
                     placeholder="Name"
                        type="text"
                        name="nameEmail"
                        value={contact.nameEmail}
                        onChange={handleChange}
                        required
                     />

                <label > <strong>Email</strong></label>
                <input 
                    className="form-control"
                    required
                    type="email"
                    name="userEmail"
                    value={contact.userEmail}
                    onChange={handleChange}
                />

                    <label><strong>Message</strong></label>
                    <textarea 
                        type="text"
                        className="form-control"
                        placeholder="message here"
                        min-rows="3"
                        name="emailDetails"
                        value={contact.emailDetails}
                        onChange={handleChange}
                    />

                    { showMessage ? <div className="alert alert-success mt-3 col-md-5 mx-auto" role="alert">Email Send Success!!</div> : ''}
            
                    <br />
                    <button type="submit" className="btn btn-purple mb-2 btn-lg btn-block">Submit</button>
                </form>
            </div>
        </Fragment>
    )
}

export default Contact;