import React from 'react';
import logoSacv from '../assets/logosacv.png';
import './component.css';

function Navbar(){

    return(
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark-mid fixed-top mb-5">
    
        <img id="logo" src={logoSacv} alt="logotype"/>
    
            <button className="navbar-toggler"
            type="button" data-toggle="collapse"
            data-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
                aria-expanded="false" 
                aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">

            <div className="navbar-nav">
                <a className="nav-item nav-link active" href="#sacv">Top <span className="sr-only">(current)</span></a>
                <a className="nav-item nav-link" href="#bio">About me</a>
                <a className="nav-item nav-link" href="#projects">Projects</a>
                <a className="nav-item nav-link" href="#contact">Contact</a>
            </div>

        </div>

    </nav>

    )
}

export default Navbar;