import React, { Fragment } from 'react';
import sergio from '../assets/sergio.jpg';
import './component.css';
import Skills from './skills';

function Bio(){

    return(

        <Fragment>
            <div className="container mt-5 mb-5" id="bio">
                <h2 className="display-4">About me</h2>
            <div className="row mx-4">

                <div className="col">

                    <img className="rounded-circle bio mb-5"
                    id="sergio" src={sergio} alt="sergio_campbell_developer"/>

                    <p className="container bio">
                    My journey as a freelance developer started since 2010, i've done 
                    formal work for a few companies in Colombia; then in 2020
                    I began to do it fully remote.

                    I've done collaborations with pretty talented people
                    within the telecommunications sector installing and repairing ADSL,
                    and of course, designing and developing inner interfaces for several companies.
                    <br /><br/>
                    I'm adventour, curious and like to work and collaborate with awesome people and 
                    improve my little details step by step in each project.
                    </p>
                </div>

                <div className="col">
                    <Skills />
                </div>

            </div>
            </div>
        </Fragment>
    

    )
}

export default Bio;