/* eslint-disable jsx-a11y/aria-role */
import React, {Fragment} from 'react';
import './component.css';


function Skills(){

    return(
        <Fragment>
        <h3 className="h2">Frameworks I use</h3>
                <strong>HTML</strong>
                <div className="progress">
                <li id="html"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>

                <strong>CSS</strong>
                <div className="progress">
                <li id="css"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>

                <strong>Javascript</strong>
                <div className="progress">
                <li id="js"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>

                <strong>Angular</strong>
                <div className="progress">
                <li id="angular"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>

                <strong>React</strong>
                <div className="progress">
                <li id="react"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>

                <strong className="">Ionic</strong>
                <div className="progress">
                <li id="ionic"><div className="progress-bar progress-bar-striped progress-bar-animated bg-pbar" 
                role="progress-bar" 
                aria-valuenow="90" 
                aria-valuemin="0" 
                aria-valuemax="100">&nbsp;</div></li>
                </div>            

        </Fragment>
    )
}

export default Skills;