import React, { useState } from 'react';
import './App.css';
import Navbar from './components/navbar';
import Header from './components/header';
import Projects from './components/projects';
import Contact from './components/contact';
import Footer from './components/footer';
import scrollreveal from 'scrollreveal';
import Bio from './components/bio';

function App() {

  const sr = scrollreveal({duration: 1000, reset: true, interval: 16});

    sr.reveal('.rounded-circle', {origin: 'bottom', delay: 500});
    sr.reveal('.container', {origin: 'bottom', delay: 500});
    sr.reveal('.col', {origin: 'bottom', delay: 100});
    sr.reveal('.mt-3', {origin: 'top', delay: 100});

  const [load, setLoad] = useState(undefined);

  setTimeout(() => {
    setLoad(true);
  }, 1000);

  return (
    <div className="App">
      {!load ? (
        <div className="d-flex justify-content-center">
          <div className="loader">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
      </div>
      ) : (
        <div>

            <div className="slider">
              <a href="https://facebook.com/iamsergiocampbell" target="_blank">Facebook  &nbsp;&nbsp;<i class="fab fa-facebook"></i></a>
              <a href="https://twitter.com/sergiocamp28" target="_blank">Twitter  &nbsp;&nbsp;<i className="fab fa-twitter "></i></a>
              <a href="https://github.com/sergiocampbell" target="_blank">Github  &nbsp;&nbsp;<i className="fab fa-github "></i></a>
              <a href="https://codepen.io/SergioCampbell" target="_blank">CodePen &nbsp;&nbsp;<i className="fab fa-codepen"></i></a>
              <a href="https://linkedin.com/in/iamsergiocampbell" target="_blank">LinkedIn  &nbsp;&nbsp;<i className="fab fa-linkedin"></i></a>
              <a href="https://www.youtube.com/channel/UCFBP6yNrCSo7FWvAwRULEbw" target="_blank">YouTube  &nbsp;&nbsp;<i className="fab fa-youtube"></i></a>
            </div>
            
          <Navbar />
          <Header />
          <Bio />
          <Projects />
          <Contact />
          <Footer />
      </div>
      )}
    </div>
  );
}

export default App;
